*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Mulish', sans-serif;
}

body{
  background: #E5E5E5;
}



h2 {
  margin-right: auto;
  margin-left: 265px;
}

h4,img{
  padding-right: 25px;
}


h5{
  margin-right: auto;
  margin-left:25px;
  font-style: normal;
font-weight: bold;
font-size: 19px;
color: #252733;
}




/* ---Mainbar and its contents--- */

.MainBar {
  height:100px;
  width: auto;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  position: relative;

}

/* ---Sidebar and its contents--- */


.sidebar{
  height: 60.63em;
  width: 15.31em;
  background-color: #363740;
  position: absolute;
  top: 0;
  left: 0;
}


.listItems{
  height: auto;
  padding: 0;
  width: 100%;
}
.listItems .list{
  width: 100%;
  height: 60px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: #A4A6B3;
  font-size: 16px;
}


#icon{
  flex: 30%;
  display: grid;
  place-items:center;
}
#title{
  flex: 70%;
}

/* logo and its description */
.logo{
  display: flex;

  align-items: center;
  margin:35px 0 70px 25px;

}
 a{
  margin:0;
  font-weight: bold;
  font-size: 25px;
  color: #A4A6B3;
opacity: 0.7;
text-decoration: none;
}




/* ---Table and its contents--- */



.table{
position: relative;
  background: #FFFFFF;
  Width :auto;
  Height :auto;
margin:0 20px 0 265px;
border: 1px solid #DFE0EB;
box-sizing: border-box;
border-radius: 8px;
}

.tableHeader{
  height:100px;
  width: auto;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 14px;
  color: #4B506D;
}



.aligntable >img{
  float: left;
}

div > p {
  margin-left: 62px;
}

p {
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.2px;
  color: #252733;
}

span {
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.1px;
  color: #c5c7cd;
}


.tablehead{
  font-size: 16px;
  font-weight: bold;
}


button {
  font-style: normal;
  font-weight: bold;
  font-size: 11px;
  line-height: 14px;
  text-align: center;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  color: #ffffff;
  border-radius: 100px;
  border: none;
  float: left;
  margin-top: 3px;
}


.Icons{
position: relative;
margin-top: 8px;
float: right;
color: #C5C7CD;
}



@media (max-width: 1000px) {
.sidebar{
  height: 92em;
}
}
